.call-modal{
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    margin: auto;
    background: #FFFFFF;
    border-radius: 30px;
    max-width: 790px;
    width: 100%;
    padding: 32px;
    height: 330px;
    display: none;
    z-index: 102;
    @media (max-width: $mq__mobile) {
        height: fit-content;  
        max-width: calc(100vw - 20px);
    }
    iframe{
        height: 100% !important;
        opacity: 1 !important;
    }
    &.is-display {
        display: block;
    }

    &__close {
        padding: 0;
        position: absolute;
        background: none;
        border: none;
        right: 24px;
        top: 24px;
        cursor: pointer;
        opacity: 0.3;
        svg {
            width: 20px;
            height: 20px;
        }
    }

    &__row{
        display: flex;
        gap: 36px;
        @media (max-width: $mq__mobile) {
            flex-direction: column-reverse;
            gap: 10px;
        }
    }
    &__amo{
        position: relative;
        height: 125px;
        max-width: 400px;
        form{
            max-width: 400px;
            position: relative;
            display: flex;
            gap: 16px;
            flex-direction: column;
            .form-field{
                padding: 0 17px;
                font-family: 'TT Hoves',sans-serif,BlinkMacSystemFont,-apple-system,'Segoe UI',Helvetica,Arial,sans-serif;
                height: 50px;
                width: 100%;
                color: black;
                font-family: 'TT Hoves';
                font-style: normal;
                font-weight: 500;
                font-size: 16px;
                line-height: 150%;
                border: 1px solid #cfcacf;
                border-radius: 16px;
                &::placeholder{
                    color: #2222224D;
                }
                &:focus, &:focus-visible{
                    outline: none;
                }
            }

            .container-field{
                position: relative;
                &::after{
                    content: '*';
                    position: absolute;
                    right: 17px;
                    top: 8px;
                    font-size: 22px;
                    color: red;
                    bottom: 0;
                    display: flex;
                    align-items: center;
                }
            }

            button{
                height: 51px;
                border: none;
                width: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
                cursor: pointer;
                color: #FFFFFF;
                background-color: #ea5757;
                border-radius: 53px;
                max-width: 160px;
                &:hover{
                    opacity: 0.5;
                    transition-duration: 0.6s;
                }
            }
        }


    }
    &__timer{
        text-align: center;
        font-weight: 700;
        font-size: 54px;
        line-height: 64px;
        color: #3055D8;
        margin-top: 2px;
    }
    &__people{
        max-width: 214px;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        @media (max-width: $mq__mobile) {
            margin: 0 auto;
        }
        picture > img{
            margin: 0 auto;
        }
        h5{
            font-weight: 600;
            font-size: 20px;
            line-height: 24px;
            text-align: center;
            margin-top: 10px;
            letter-spacing: 0.01em;
        }
        p{
            margin: 0;
            opacity: 0.7;
            font-weight: 400;
            font-size: 14px;
            line-height: 16px;
            text-align: center;
            letter-spacing: 0.01em;
            margin-top: 4px;
        }
    }
    &__form{
        max-width: 476px;
        width: 100%;
        h3{
            font-weight: 700;
            font-size: 32px;
            line-height: 105%;
            margin-bottom: 24px;
            @media (max-width: $mq__mobile) {
                font-size: 26px;
                margin-bottom: 12px;
                text-align: center;
            }
            b{
                color: #3055D8;
            }
        }
        p{
            margin-top: 24px;
            font-weight: 500;
            font-size: 12px;
            line-height: 130%;
            opacity: 0.5;
            @media (max-width: $mq__mobile) {
                margin-top: 12px;
            }
        }
    }
}