.about {
    &__body {
        display: -webkit-flex;
        display: flex;
        justify-content: space-between;
        align-items: center;
        @media (max-width: $mq__laptop) {
            flex-direction: column;
        }
    }

    &__list {
        margin-right: 54px;
        display: grid;
        grid-auto-columns: 4fr;
        gap: 32px;
        grid-template-columns: repeat(4, minmax(0, 1fr));
        flex-shrink: 0;
        @media (max-width: $mq__laptop) {
            width: 100%;
            margin-right: 0;
            grid-auto-columns: 2fr;
            gap: 16px;
            grid-template-columns: repeat(2, minmax(0, 1fr));
        }
    }

    &__card {
        text-align: center;
        padding: 32px;
        background: #FFFFFF;
        box-shadow: 0 0 20px #DBDBDB;
        border-radius: 30px;
        transition: $animation__duration_slow;
        max-width: 218px;

        &:hover,
        &:focus {
            box-shadow: 0 0 30px #BEBEBE;
        }

        @media (max-width: 1279px) {
            max-width: 100%;
        }
        @media (max-width: $mq__mobile) {
            padding: 24px 0;
        }

        &_caption {
            margin: 0 0 12px;
            font-weight: 600;
            font-size: 48px;
            line-height: 57px;
            letter-spacing: 0.03em;
            color: #3055D8;
            @media (max-width: $mq__mobile) {
                margin: 0 0 4px;
                font-size: 22px;
                line-height: 26px;
            }
        }

        &_text {
            font-weight: 500;
            font-size: 16px;
            line-height: 150%;
            text-align: center;
            letter-spacing: 0.01em;
            color: #222222;
            @media (max-width: $mq__mobile) {
                font-size: 14px;
                line-height: 16px;
            }
        }
    }

    &__text {
        font-weight: 500;
        font-size: 16px;
        line-height: 150%;
        letter-spacing: 0.01em;
        color: #222222;
        opacity: 0.8;
        @media (max-width: $mq__mobile) {
            font-size: 14px;
        }
        @media (max-width: $mq__laptop) {
            margin-top: 24px;
        }
    }
}

.about-pages{
    .about__body{
        margin-top: -16px;
        align-items: initial;
        @media (max-width: $mq__mobile) {
            margin-top: 0;
        }
    }

    .about__list{
        margin-right: 48px;
        grid-auto-columns: 2fr;
        grid-template-columns: repeat(2, minmax(0, 1fr));
    }

    .about__card{
        max-width: 340px;
        @media (max-width: $mq__mobile) {
            padding: 10px;
            padding-top: 16px;
        }

        &_caption{
            font-size: 64px;
            line-height: 75px;
            margin: 0 0 14px;
            @media (max-width: $mq__mobile) {
                font-size: 30px;
                line-height: 35px;
                margin-bottom: 0;
                margin-bottom: 6px;
            }
        }

        &_text{
            font-size: 18px;
            @media (max-width: $mq__mobile) {
                font-size: 12px;
                line-height: 14px;
            }
        }
    }

    .about__text{
        font-size: 20px;
        @media (max-width: $mq__mobile) {
            margin-top: 16px;
            font-size: 14px;
            line-height: 150%;
        }
    }
    
}

.seo-page, .template {
    .about {
        &__list {
            display: flex;
            flex-shrink: initial;
            margin: 0;
            @media (max-width: $mq__mobile) {
                display: grid;
            }
        }
        &__card {
            padding: 0;
            width: 25%;
            box-shadow: none;
            max-width: initial;
            text-align: left;
            @media (max-width: $mq__mobile) {
                width: 100%;
                text-align: center;
            }

            &_caption {
                font-size: 96px;
                line-height: 105%;
                @media (max-width: $mq__mobile) {
                    font-size: 28px;
                }
            }

            &_text {
                text-align: left;
                font-size: 18px;
                line-height: 140%;
                color: rgba(34, 34, 34, 0.6);
                @media (max-width: $mq__mobile) {
                    text-align: center;
                }
            }
        }
    }
}

.template {
    .about-description {
        padding: 48px 0;

        @media (max-width: $mq__mobile) {
            padding: 16px 0;
        }
        &__content {
            flex-direction: column;
            max-width: 1200px;

            h2{
                color: #FFF;
                font-size: 50px;
                font-style: normal;
                font-weight: 700;
                line-height: normal;
                margin-bottom: 24px;

                @media (max-width: $mq__mobile) {
                    font-size: 24px;
                }
            }

            h3 {
                color: #FFF;
                font-size: 30px;
                margin-bottom: 6px;

                @media (max-width: $mq__mobile) {
                    font-size: 20px;
                }
            }

            p{
                color: #EEF2F5;
                font-size: 20px;
                font-style: normal;
                font-weight: 500;
                line-height: 150%;
                margin-bottom: 16px;

                @media (max-width: $mq__mobile) {
                    font-size: 16px;
                }

                a {
                    color: white;
                    font-weight: 600;
                }
            }

            ul {
                display: flex;
                flex-direction: column;
                gap: 16px;
                li{
                    color: #EEF2F5;
                    font-size: 20px;
                    font-style: normal;
                    font-weight: 500;
                    line-height: 150%;

                    @media (max-width: $mq__mobile) {
                        font-size: 16px;
                    }
                }
            }
        }
    }
}