@keyframes opacity {
    0% {opacity: 0;}
    100% {opacity: 1;}
}

.nav {
    $item-gap: 16px;
    $item-gap_mobile: 10px;

    font-variant: small-caps;
    font-size: var(--font-size_nav);

    & &__list {
        padding: 0;
        margin: 0;
        list-style-type: none;
        display: -webkit-flex;
        display: flex;

        @media (max-width: $mq__tablet) {
            flex-direction: column;
            padding: 26px 0;
        }
    }
    li .drop{
        position: relative;
        padding-right: 32px;
        cursor: pointer;
        @media (max-width: $mq__tablet) {
            width: fit-content;
            padding-right: 0;
        }
        &::after{
            content: '';
            display: block;
            position: absolute;
            bottom: 36px;
            right: 12px;
            width: 20px;
            height: 20px;
            background-position: 0 0;
            background-repeat: no-repeat;
            background-image: url('data:image/svg+xml;base64,');
            @media (max-width: $mq__tablet) {
                bottom: 0px;
                right: -20px;
                transform: rotate(-90deg);
            }
        }
        &:hover, &:focus-within{
            &::after{
                @media (max-width: $mq__tablet) {
                    transform: rotate(0deg);
                    bottom: 2px;
                    transition: all .6s ease;
                }
            }
        }
    }
    & &__item {
        display: block;
        margin: 0;
        padding: 0;
        position: relative;
        z-index: 1;
        &:hover > ul, &:focus-within > ul, &:focus > ul{
            animation: opacity .6s forwards;
            display: flex;
            flex-direction: column;
            opacity: 1;
            gap: 12px;
            @media (max-width: $mq__tablet) {
                position: initial;
                background: none;
                padding: 0;
                gap: 12px;
                box-shadow: none;
                margin-left: 12px;
            }
        }

        &:first-child {
            @media (max-width: $mq__tablet) {
                z-index: 2;
            }
        }

        &:hover > .nav__big, &:focus-within > .nav__big{
            animation: opacity .6s forwards;
            opacity: 1;
            display: flex;
        }

        .nav__big {
            position: absolute;
            width: max-content;
            margin: 0;
            background: #FFFFFF;
            border-radius: 0 0 10px 10px;
            padding: 36px;
            display: none;
            gap: 40px;
            opacity: 0;
            box-shadow: 0 0 20px #dbdbdb;

            @media (max-width: $mq__tablet) {
                width: auto;
                position: initial;
                background: none;
                border-radius: 0;
                padding: 0;
                gap: 0;
                box-shadow: initial;
                margin-left: 12px;
            }

            ul {
                opacity: 1;
                position: initial;
                display: flex;
                flex-direction: column;
                gap: 12px;
                padding: 0;
                box-shadow: initial;
                min-width: 255px;

                @media (max-width: $mq__tablet) {
                    background: #282A32;
                    min-width: initial;
                }

                li:hover > a {
                    opacity: 1;
                    color: #3055D8;

                    @media (max-width: $mq__tablet) {
                        color: white;
                    }
                }

                ul {
                    padding-left: 20px;
                    margin-top: 8px;
                    display: flex;
                    gap: 8px;
                    display: none;
                    opacity: 0;

                    @media (max-width: $mq__tablet) {
                        padding-left: 12px;
                    }

                    li > a {
                        color: rgba(34, 34, 34, 0.60);

                        @media (max-width: $mq__tablet) {
                            color: white;
                        }
                    }
                }
            }

            .nav__big > .nav__big-right li:hover > ul {
                animation: opacity .6s forwards;
                display: flex !important;
                opacity: 1;

                @media (max-width: $mq__tablet) {
                    position: initial;
                    margin-left: 12px;
                }
            }

            .nav__big-left {
                gap: 24px;

                @media (max-width: $mq__tablet) {
                    gap: 12px;
                }

                li {
                    a {
                        padding-right: 0;
                        &::after {
                            content: none;
                            
                            @media (max-width: $mq__tablet) {
                                content: '';
                            }
                        }
                    }

                    ul {
                        display: none;
                    }
                }

                li:hover > ul {
                    @media (max-width: $mq__tablet) {
                        animation: opacity .6s forwards;
                        display: flex !important;
                        opacity: 1;
                    }
                }
            }

            #menuSubItem1 {
                @media (max-width: $mq__tablet) {
                    display: none;
                    padding-left: 24px;
                }
            }

            #menuSubItem2, #menuSubItem3 {
                display: none;
            }

            &-left {
                display: flex;
                flex-direction: column;
                gap: 24px;

                @media (max-width: $mq__tablet) {
                    gap: 10px;
                    padding-left: 12px;
                }

                a {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    min-width: 280px;
                    gap: 10px;

                    @media (max-width: $mq__tablet) {
                        min-width: initial;
                        justify-content: initial;

                        &::after {
                            bottom: -3px;
                        }
                    }

                    &:hover {
                        opacity: 1;

                        .link {
                            color: #3055D8;

                            @media (max-width: $mq__tablet) {
                                color: white;
                            }
                        }

                        svg > path {
                            fill: #3055D8;

                            @media (max-width: $mq__tablet) {
                                fill: white;
                            }
                        }
                    }

                    .link {
                        color: #222;
                        font-size: 18px;
                        font-weight: 500;
                        transition: color .6s;

                        @media (max-width: $mq__tablet) {
                            color: white;
                            font-size: 14px;
                            font-weight: 400;
                        }
                    }

                    p {
                        color: rgba(51, 51, 51, 0.60);
                        font-size: 12px;

                        @media (max-width: $mq__tablet) {
                            display: none;
                        }
                    }

                    svg {
                        width: 24px;
                        height: 24px;
                        transition: all .6s;

                        @media (max-width: $mq__tablet) {
                            display: none;
                        }

                        path {
                            transition: all .6s;

                            @media (max-width: $mq__tablet) {
                                fill: white;
                            }
                        }
                    }
                }
            }
        }

        ul{
            position: absolute;
            width: max-content;
            margin: 0;
            background: #FFFFFF;
            border-radius: 0 0 10px 10px;
            padding: 24px;
            display: none;
            opacity: 0;
            box-shadow: 0 0 20px #dbdbdb;
            @media (max-width: $mq__tablet) {
                width: auto;
            }
            li{
                list-style: none;
                margin: 0;

                &:hover > ul {
                    animation: opacity .6s forwards;
                    display: flex;
                    opacity: 1;
                }

                ul {
                    margin-top: 8px;
                    opacity: 0;
                    position: initial;
                    box-shadow: initial;
                    display: none;
                    flex-direction: column;
                    gap: 8px;
                    border-radius: 0;
                    padding: 0;
                    padding-left: 20px;

                    @media (max-width: $mq__tablet) {
                        background: none;
                        padding-left: 12px;
                    }

                    li > a {
                        color: rgba(34, 34, 34, 0.60);

                        @media (max-width: $mq__tablet) {
                            color: rgba(238, 242, 245, 0.60);
                        }
                    }
                }

                a{
                    display: block;
                    font-size: 14px;
                    line-height: 110%;
                    letter-spacing: 0.01em;
                    color: #222;
                    font-weight: 500;
                    @media (max-width: $mq__tablet) {
                        font-size: 14px;
                        line-height: normal;
                        font-variant: small-caps;
                        color: #EEF2F5;
                        font-weight: 400;
                    }
                    &:hover {
                        opacity: 1;
                        color: #3055D8;

                        @media (max-width: $mq__tablet) {
                            color: white;
                        }
                    }
                }
            }
        }
    }

    & &__item.is-no-active &__link {
        color: var(--color_disabled);

        &:hover {
            color: var(--color__link_hover);
        }
    }

    & &__link {
        margin: 0;
        padding: 36px 8px;
        display: block;
        border-bottom: none;
        color: #eef2f5;
        line-height: 16px;
        font-size: 16px;

        &:active,
        &:focus,
        &:hover {
            opacity: 0.5;
        }

        @media (max-width: $mq__laptop) and (min-width: $mq__tablet) {
            padding: 36px 8px;
            &:last-child {
                padding: 36px 8px;
            }
        }
        @media (max-width: $mq__tablet) {
            padding: 0;
            margin: 10px 0;
            font-size: 16px;
            height: 24px;
            align-items: center;
            display: flex;
            font-weight: 500;
            &:last-child {
                padding: 0;
            }
        }
    }
}

.footer-nav {
    &__list {
        max-width: 445px;
        padding: 0;
        margin: 0;
        list-style-type: none;
        display: -ms-flexbox;
        display: -webkit-flex;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        @media (max-width: $mq__tablet) {
            justify-content: center;
            gap: 16px;
        }
    }

    &__item {
        display: block;
        margin: 0;
        padding: 0;
    }

    &__link {
        margin: 0;
        padding: 8px 8px;
        display: block;
        border-bottom: none;
        color: #eef2f5;
        line-height: 16px;
        white-space: nowrap;
        @media (max-width: $mq__laptop) and (min-width: $mq__tablet) {
            padding: 8px 8px;
        }
        @media (max-width: $mq__tablet) {
            padding: 0;
        }
    }

    &__lang {
        display: none;

        @media (max-width: $mq__tablet) {
            display: -webkit-flex;
            display: flex;
            align-items: center;
            color: #eef2f5;

            .lang__link {
                color: #eef2f5;
                opacity: 0.5;
            }
        }
    }
}