.header {
    display: -webkit-flex;
    display: flex;
    justify-content: flex-start;
    position: relative;
    width: 100%;
    font-size: var(--font-size_nav);
    align-items: center;


    @media (max-width: $mq__tablet) {
        height: 60px;
        &.is-open {
            .header__menu {
                left: 0;
            }
        }
    }

    &_style_cn &__buttons {

        @media (max-width: $mq__tablet) {
            top: 9px;
        }

        @media (max-width: $mq__tablet) {
            top: 4px;
        }
    }

    &_style_fix-height {
        height: 77px;
    }

    &__item {
        &_btn {
            background: none;
            border: none;
            &:hover {
                opacity: 0.8;
            }

            @media (max-width: $mq__tablet) {
                span {
                    display: none;
                }
            }
            span {
                background-color: #3055D8;
                color: #EEF2F5;
                margin-left: 16px;
                padding: 0 28px;
            }
             
            > svg {
                display: none;
                @media (max-width: $mq__tablet) {
                    display: block;
                    width: 32px;
                    height: 32px;
                    background: #EEF2F5;
                    border-radius: 50%;
                }
            }
        }

        &_logo {
            padding-right: 30px;
            display: -webkit-flex;
            display: flex;
            align-items: center;
            flex-grow: 1;
            @media (max-width: $mq_xxs) {
                padding-right: 10px;
            }
        }

        &_content {
            display: -webkit-flex;
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            justify-content: space-between;
            width: 100%;

            @media (max-width: $mq__tablet) {
                padding-right: 50px;
            }
        }
    }

    &__lang {
        display: -webkit-flex;
        display: flex;
        align-items: center;

        @media (max-width: $mq__tablet) {
            position: absolute;
            top: 85px;
            right: 0;
        }

        @media (max-width: $mq__tablet) {
            top: 69px;
        }
    }

    &__info {
        display: -webkit-flex;
        display: flex;
        flex-direction: column;
        position: relative;
        top: 26px;
    }

    &__buttons {
        display: -webkit-flex;
        display: flex;
        align-items: center;
    }

    &__menu-button {
        display: none;
        margin-right: 20px;

        @media (max-width: $mq__tablet) {
            display: -webkit-flex;
            display: flex;
        }

        @media (max-width: $mq_xxs) {
            margin-right: 10px;
        }
    }

    &__qr {
        width: 60px;
        height: 60px;
        margin-right: 10px;
    }

    &__info-wrapper {
        margin-left: 8px;
        display: -webkit-flex;
        display: flex;
        justify-content: center;
        flex-direction: column;
        @media (max-width: $mq__tablet) {
            margin-left: 0;
        }
    }

    &__info-link {
        font-weight: 400;
        font-size: 14px;
        line-height: 16px;
        color: #EEF2F5;
        &:first-child {
            margin-bottom: 6px;
        }
    }

    .nav {
        margin-right: 16px;
        position: relative;
        @media (max-width: $mq__tablet) {
            /*
            flex-grow: 1;
            margin-right: 0;
            min-width: 100%;
            */
            display: none;
        }
        &:after {
            content: '';
            width: 1px;
            height: 17px;
            position: absolute;
            right: 0;
            top: calc(50% - 8px);
            background: #EEF2F5;
            @media (max-width: $mq__tablet) {
                display: none;
            }
        }
    }

    &__socials {
        display: inline-flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        margin-right: 16px;
        height: 38px;
        font-weight: 400;
        font-size: 10px;
        line-height: 12px;
        color: #EEF2F5;
        @media (max-width: $mq__laptop) and (min-width: $mq__tablet) {
            margin-right: 6px;
        }

        .telegram-contact {
            background: transparent url('../img/telegram.svg') center no-repeat;
            width: 20px;
            height: 24px;
            display: block;
            @media (max-width: $mq__tablet) {
                width: 25px;
                height: 32px;
            }
        }

        .whatsapp-contact {
            background: transparent url('../img/whatsapp.svg') center no-repeat;
            width: 24px;
            height: 24px;
            display: block;
            @media (max-width: $mq__tablet) {
                width: 32px;
                height: 32px;
            }
        }

        &-list {
            display: -webkit-flex;
            display: flex;
            @media (max-width: $mq__tablet) {
                margin-bottom: 24px;
            }
        }
        &-mobile {
            display: none;
            .header__socials {
                margin-right: 0;
                margin-left: 6px;
            }
            .telegram-contact {
                width: 32px;
                height: 32px;
            }

            .whatsapp-contact {
                width: 38px;
                height: 38px;
                margin-top: -3px;
            }
            @media (max-width: $mq__tablet) {
                display: -webkit-flex;
                display: flex;
                margin-top: 6px;
            }
        }
    }

    &__menu {
        display: -webkit-flex;
        display: flex;
        justify-content: end;
        position: relative;
        width: 100%;
        align-items: center;
        @media (max-width: $mq__tablet) {
            position: fixed;
            left: -320px;
            top: 1px;
            height: 100vh;
            width: 320px;
            padding: 21px 13px;
            align-items: start;
            flex-direction: column;
            justify-content: start;
            background: #282A32;
            z-index: 100;
            transition: all .3s ease;
        }

        .menu-mobile {
            display: none;

            @media (max-width: $mq__tablet) {
                display: block;
            }
        }
    }

}

.section_header .section__body {
    @media (max-width: $mq__laptop) {
        padding: 0 16px;
    }
}