.projects {
    overflow: hidden;
    .projects-btn {
        display: flex;
        padding: 14px 73.5px 14px 73.5px;
        justify-content: center;
        align-items: center;
        background: #3055d8;
        color: #eef2f5;
        border: 1.5px solid #3055d8;
        border-radius: 20px;
        font-weight: 500;
        font-size: 16px;
        line-height: 149.3%;
        letter-spacing: 0.03em;
        transition: 0.6s;
        position: absolute;
        right: 0;
        bottom: 0;
        @media (max-width: $mq__tablet) {
            right: 0;
            padding: 14px 28px 14px 28px;
        }
    }
    &__wrapper {
        display: flex;
        flex-wrap: wrap;
        gap: 24px;
        @media (max-width: $mq__tablet) {
            gap: 0;
            flex-wrap: nowrap;
        }
        .projects__card {
            @media (max-width: $mq__tablet) {
                margin: 0 12px;
                &:first-child {
                    margin-left: 0;
                }
                &:last-child {
                    margin-right: 0;
                }
            }
        }
    }
    &__row {
        position: relative;
        padding-bottom: 83px;
    }
    &__box {
        overflow: initial;
    }
    &__btn {
        display: none !important;
        @media (max-width: $mq__tablet) {
            display: flex !important;
        }
    }
    &__text {
        p {
            margin: 0 0 5px;
            font-weight: 400;
            font-size: 14px;
            line-height: 16px;
        }
        h3 {
            margin: 0;
            font-weight: 600;
            font-size: 28px;
            line-height: 33px;
        }
    }
    &__decor {
        &-item-1, &-item-2, &-item-3, &-item-4, &-item-5, &-item-6, &-item-7, &-item-8, &-item-9, &-item-10, &-item-11, &-item-12, &-item-13, &-item-14, &-item-15, &-item-16, &-item-17, &-item-18, &-item-19, &-item-20, &-item-21, &-item-22, &-item-23, &-item-24, &-item-25, &-item-26, &-item-27, &-item-28, &-item-29, &-item-30, &-item-31, &-item-32, &-item-33, &-item-34, &-item-35, &-item-36, &-item-37, &-item-38, &-item-39 {
            position: absolute;
            transition-duration: 1s;
        }
    }
    &__card {
        box-shadow: 0px 0px 20px #dbdbdb;
        border-radius: 30px;
        height: 400px;
        max-width: calc((100% / 4) - 18px);
        width: 100%;
        color: initial;
        position: relative;
        @media (max-width: $mq__tablet) {
            max-width: 288px !important;
            height: 360px;
        }

        img {
            display: initial;
        }

        h3 {
            margin-bottom: 0;
        }

        &:hover,
        &:visited,
        &:focus {
            opacity: 1;
        }
    }
    &__text {
        position: absolute;
        padding-right: 12px;
        bottom: 15px;
        left: 15px;
        z-index: 2;
        transition: all 1s ease;
        p {
            font-weight: 400;
            font-size: 14px;
            line-height: 16px;
            margin-bottom: 5px;
        }
        h3 {
            font-weight: 600;
            font-size: 28px;
            line-height: 33px;
            @media (max-width: $mq__tablet) {
                font-size: 20px;
                line-height: 24px;
            }
        }
    }

    .b1 {
        background: #272727;
        &::after {
            opacity: 0;
            content: "";
            width: 100%;
            height: 100%;
            position: absolute;
            left: 0;
            top: 0;
            background: linear-gradient(180deg, #200065 0%, #4d004f 100%);
            transition-duration: 1s;
            z-index: 1;
            border-radius: 30px;
        }
        .projects__img picture {
            z-index: 2;
            position: absolute;
            top: 101px;
            text-align: center;
            left: 0;
            right: 0;
            transition-duration: 1s;
            &::after {
                position: absolute;
                left: 0;
                right: 0;
                margin: 0 auto;
                top: -10px;
                content: "";
                max-width: 270px;
                width: 100%;
                height: 156px;
                background: none;
                border-radius: 16px;
                border: 1px solid white;
            }
        }
        .projects__decor {
            &-item-1 {
                z-index: 2;
                left: 7px;
                top: 2px;
                svg {
                    border-radius: 100%;
                    path {
                        transition-duration: 1s;
                    }
                }
            }
            &-item-2 {
                z-index: 2;
                top: 19px;
                right: 15px;
                svg {
                    border-radius: 100%;
                    path {
                        transition-duration: 1s;
                    }
                }
            }
            &-item-3 {
                z-index: 2;
                left: 13px;
                bottom: 100px;
                svg {
                    border-radius: 100%;
                    path {
                        transition-duration: 1s;
                    }
                }
            }
            &-item-4 {
                z-index: 2;
                right: 22px;
                bottom: 70px;
            }
            &-bg {
                z-index: 2;
                picture {
                    position: absolute;
                    left: 0;
                    top: 0;
                    height: 100%;
                    width: 100%;
                    &::after {
                        content: none;
                    }
                }
            }
        }
        .projects__text {
            color: white;
        }
        @media (min-width: $mq__tablet) {
            &:hover {
                &::after {
                    opacity: 1;
                }
                .projects__img picture {
                    transform: scale(1.05);
                }
                .projects__decor {
                    &-item-1 {
                        transform: scale(1.3) rotate(10deg)
                            translateX(-10px);
                        svg {
                            border-radius: 100%;
                            path {
                                filter: drop-shadow(0px 0px 5px #f7931a);
                            }
                        }
                    }
                    &-item-2 {
                        transform: scale(1.25) rotate(10deg)
                            translateX(20px) translateY(-20px);
                        svg {
                            border-radius: 100%;
                            path {
                                filter: drop-shadow(0px 0px 5px #627eea);
                            }
                        }
                    }
                    &-item-3 {
                        transform: scale(1.3) rotate(-5deg)
                            translateX(-15px);
                        svg {
                            border-radius: 100%;
                            path {
                                filter: drop-shadow(0px 0px 5px #53ae94);
                            }
                        }
                    }
                }
            }
        }
    }

