                    }
                    &-item-5 {
                        width: 150px;
                        height: 304px;
                        transform: rotate(-9deg);
                        top: 9px;
                        left: 19px;
                        transition: all 1s ease;
                    }
                    &-item-6 {
                        width: 140px;
                        height: 289px;
                        transform: rotate(8deg);
                        top: 60px;
                        left: 159px;
                        transition: all 1s ease;
                    }
                    &-item-7 {
                        opacity: 1;
