                    }
                }
            }
        }
        @media (min-width: $mq__tablet) {
            &:hover {
                transition: all 1s ease;
                background: #2f005e;
                .projects__text {
                    transition: color 1s ease;
                    color: white;
                }
                .projects__img picture {
                    transform: scale(1.1) translateY(10px);
                }
                .projects__decor {
                    &-bg picture {
                        transform: scale(1);
                    }
                    &-item-1 {
                        z-index: 2;
                        transform: scale(0.25) translateY(-80px)
                            translateX(115px);
                        opacity: 0;
                    }
                    &-item-2 {
                        z-index: 2;
                        transform: scale(0.65) translateY(30px)
                            translateX(-270px) rotate(-8deg);
                        opacity: 0;
                    }
                }
            }
        }
    }

