.project-amo {
    padding-top: 20px;
    padding-left: 20px;
    padding-right: 20px;
    height: 290px;
    position: fixed;
    display: none;
    margin: auto;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    z-index: 9999;
    max-width: 361px;
    width: 100%;
    border-radius: 30px;
    background: white;
    &.is-display {
        display: block;
    }
    &__wrapper {
        position: relative;
        height: 100%;
    }
    &__close {
        position: absolute;
        background: none;
        border: none;
        right: 0px;
        top: 8px;
        cursor: pointer;
        svg {
            width: 32px;
            height: 32px;
        }
    }
}
.projects-page .projects-btn{
    display: none;
}