.quiz__chat-middle-message-audio {
    background-color: #3055d8;
    color: white;
    padding: 10px;
    border-radius: 16px;
    margin-top: 0;
    margin-bottom: 32px;
    position: relative;
    width: 100%;
    height: 62px;
    cursor: pointer;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: start;
    -ms-flex-pack: start;
    justify-content: flex-start;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    transition: $animation__duration_slow;
    animation: waiting 4s infinite linear;

    &:after {
        content: ' ';
        position: absolute;
        bottom: -25px;
        left: 17px;
        font-size: 14px;
        line-height: 150%;
        color: rgba(0, 0, 0, 0.4);
    }

    &.playing {
        animation: none;

        .quiz__chat-middle-message-audio-play,
        .quiz__chat-middle-message-audio-line {
            animation: none;
        }

        &.audio1 {
            .quiz__chat-audio-box {
                animation: playing 12s linear;
            }
        }

        &.audio2 {
            .quiz__chat-audio-box {
                animation: playing 16s linear;
            }
        }

        &.audio3 {
            .quiz__chat-audio-box {
                animation: playing 12s linear;
            }
        }
    }

    &.pause {
        .quiz__chat-audio-box {
            animation-play-state: paused !important;
        }
    }
}

