                    }
                }

                @media (max-width: $mq__mobile) {
                    font-size: 0;
                }
            }
        }
    }
}

.seo-page, .template {
    .feedback-seo {
        .feedback__left {
            max-width: 482px;
            box-sizing: content-box;
        }
        .quote__photo {
            top: auto;
            right: auto;
            max-width: 250px;
            position: relative;

            @media (max-width: $mq__mobile) {
                margin: 0 auto;
            }

            p {
                font-weight: 500;
                font-size: 20px;
                line-height: 150%;
                text-align: center;
                color: #222222;
                opacity: 0.8;
            }
        }
        .quote__name {
            margin: 14px 0 12px;
            font-weight: 700;
            font-size: 20px;
            line-height: 110%;
            text-align: center;
            color: #3055D8;
        }
        .quote__position {
            font-weight: 700;
            font-size: 20px;
            line-height: 110%;
            text-align: center;
            color: #3055D8;
        }
    }
}

.template {
    .feedback__h {
        font-size: 42px;
        margin-bottom: 24px;

        @media (max-width: $mq__mobile) {
            margin-bottom: 16px;
            font-size: 24px;
            line-height: 28px;
        }
    }
}

@keyframes visible {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}