.promo {
    padding: 89px 0;
    overflow: hidden;
    position: relative;
    @media (max-width: $mq__tablet) {
        padding: 84px 0 48px;
    }

    /*
    &-projects{
        padding: 221.5px 0;
        @media (max-width: $mq__tablet) {
            padding: 64px 0 78px;
        }
    }
    */

    &__title {
        max-width: 1150px;
        color: rgba(255, 255, 255, 0.9);
        text-shadow: 0 0 40px rgba(0, 0, 0, 0.4);
        font-size: 55px;
        line-height: 65px;
        letter-spacing: 0.01em;
        @media (max-width: $mq__tablet) {
            font-size: 32px;
            line-height: 130%;
        }
    }

    &__text {
        margin: 0 0 64px;
        max-width: 1150px;
        font-weight: 400;
        font-size: 36px;
        line-height: 140%;
        color: rgba(255, 255, 255, 0.9);
        text-shadow: 0 0 40px rgba(0, 0, 0, 0.4);
        letter-spacing: 0.01em;
        @media (max-width: $mq__tablet) {
            margin-bottom: 48px;
            font-size: 16px;
            line-height: 130%;
        }
        &-small {
            margin: 0 0 32px;
            max-width: 1150px;
            font-weight: 400;
            font-size: 26px;
            line-height: 140%;
            color: rgba(255, 255, 255, 0.9);
            text-shadow: 0 0 40px rgba(0, 0, 0, 0.4);
            letter-spacing: 0.01em;
            @media (max-width: $mq__tablet) {
                margin-bottom: 24px;
                font-size: 16px;
                line-height: 130%;
            }
        }
    }

    &__btn.button {
        font-weight: 500;
        font-size: 20px;
        height: 62px;
        line-height: 62px;
        padding: 0 36px;
        @media (max-width: $mq__tablet) {
            font-size: 12px;
            height: 38px;
            line-height: 38px;
            padding: 0 20px;
        }
        @media (max-width: 767px) {
            display: table;
        }
        &.promo__btn-blue {
            margin-left: 32px;
            background: #3055d8;
            @media (max-width: 767px) {
                margin-left: 0;
                margin-top: 12px;
            }
        }
    }

    &__img {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}

.promo-pages{
    .promo__title{
        margin-bottom: 64px;
        @media (max-width: $mq__tablet) {
            margin-bottom: 104px;
        }
    }
}

.seo-page {
    .section__description {
        max-width: 1200px;
    }

    .promo {
        padding: 103px 0;
        background: rgba(34, 34, 34, 0.95);
        &__icon-top {
            position: absolute;
            top: 0;
            right: 0;
            mix-blend-mode: lighten;
        }
        &__icon-bottom {
            position: absolute;
            bottom: 0;
            left: 0;
            mix-blend-mode: lighten;
        }

        &__btn {
            z-index: 1;
        }

        .breadcrumbs {
            margin-bottom: 64px;
            padding: 0;
            z-index: 1;

            a {
                color: rgba(255, 255, 255, 0.9);
            }

            &__text {
                color: rgba(255, 255, 255, 0.3);
            }
        }
    }
}

.template {
    .promo {
        padding: 84px 0;
        background: rgba(34, 34, 34, 0.95);

        @media (max-width: $mq__mobile) {
            padding: 84px 0 48px;
        }

        &__title {
            margin-bottom: 16px;
        }
        &__desc {
            max-width: 1070px;
            color: rgba(255, 255, 255, 0.90);
            text-shadow: 0px 0px 40px rgba(0, 0, 0, 0.40);
            font-size: 24px;
            font-style: normal;
            font-weight: 400;
            line-height: 140%;
            letter-spacing: 0.24px;
            margin-bottom: 64px;

            @media (max-width: $mq__mobile) {
                font-size: 16px;  
            }
        }

        &__icon-top {
            position: absolute;
            top: 0;
            right: 0;
            mix-blend-mode: lighten;
        }
        &__icon-bottom {
            position: absolute;
            bottom: 0;
            left: 0;
            mix-blend-mode: lighten;
        }

        &__btn {
            z-index: 1;
        }

        .breadcrumbs {
            margin-bottom: 16px;
            margin-top: 0;
            padding: 0;
            z-index: 1;

            a {
                color: rgba(255, 255, 255, 0.9);
            }

            &__text {
                color: rgba(255, 255, 255, 0.3);
            }
        }
    }
}