.review-widget_net {
    margin-top: 60px;
}

review-lab .widget__pag {
    z-index: 2 !important;
}

.review-tabs {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    border-radius: 16px;
    max-width: fit-content;
    background-color: rgba(43, 43, 43, 0.05);
    margin-bottom: 20px;
    padding: 3px;
    &__item {
        border-radius: 16px;
        transition: background-color 0.15s;
        padding: 8px 16px;
        cursor: pointer;
        display: flex;
        align-items: center;
        gap: 5px;
        &:hover {
            background-color: rgba(43, 43, 43, 0.05);
        }
        svg {
            max-width: 20px;
            height: 20px;
        }
    }
    .active {
        background: white;
        cursor: default;
    }
}

.review-all{
    display: none;
    align-items: center;
    gap: 5px;
    margin-bottom: 20px;
    &__stars{
        display: flex;
        align-items: center;
        gap: 2px;
    }
    &.active{
        display: flex;
    }
    P{
        margin: 0;
        b{
            font-weight: 600;
        }
    }
}

.review-slider-all {
    position: relative;
    margin: 0 -30px;
    @media (max-width: $mq__mobile) {
        margin: 0;
    }
    .js-horScroll-review-all > .swiper-wrapper,
    .js-horScroll-review-video > .swiper-wrapper,
    .js-horScroll-review-yandex > .swiper-wrapper,
    .js-horScroll-review-google > .swiper-wrapper {
        padding: 30px;
        @media (max-width: $mq__mobile) {
            padding: 30px 8px;
        }
    }

    .js-horScroll-review-yandex > .swiper-wrapper > .reviews__card {
        height: 362px;
        justify-content: initial;
        &-open {
            height: auto !important;
        }
        @media (max-width: $mq__mobile) {
            height: 411px;
        }
    }
    .js-horScroll-review-google > .swiper-wrapper > .reviews__card {
        height: 305px;
        justify-content: initial;
        &-open {
            height: auto !important;
        }
        @media (max-width: $mq__mobile) {
            height: 328px;
        }
    }
    .js-horScroll-review-all > .swiper-wrapper > .reviews__card {
        height: 362px;
        justify-content: initial;
        @media (max-width: $mq__mobile) {
            height: 352px;
        }
        &-open {
            height: auto !important;
            @media (max-width: $mq__mobile) {
                height: auto;
            }
        }
        .reviews__card2-stars {
            @media (max-width: $mq__mobile) {
                margin-bottom: 5px;
            }
        }
    }
    .js-horScroll-review-all > .swiper-wrapper > [data-videoid],
    .js-horScroll-review-video > .swiper-wrapper > [data-videoid] {
        @media (max-width: $mq__mobile) {
            height: max-content;
            justify-content: initial;
        }
    }
    .review-btns-prev,
    .review-btns-next {
        button {
            display: none;
        }
        button.active {
            display: flex;
        }
        button.swiper-button-disabled {
            display: none;
        }
    }
    .js-tab-content {
        display: none;
    }
    .active {
        display: block;
        @media (max-width: $mq__mobile) {
            overflow: initial;
        }
    }
}

@media (max-width: $mq__mobile) {
    .review-sliders{
        .review-slider-all__item, .review__card{
            width: 340px !important;
        }
        .review__card{
            justify-content: space-between !important;
        }
        .review__name{
            max-width: 200px;
        }
    }
}
