// Media Queries
$mq__page-max: 1440px;
$mq__laptop: 1300px;
$mq__tablet: 1024px;
$mq__mobile: 768px;
$mq__mobile-nav: 1020px;
$mq_xs: 424px;
$mq_xxs: 374px;

$mq__header-vert: 1125px;
$mq__header-content-vert: 680px;
$mq__section-teaser_vert: 1275px;
$mq__map-vert: 1000px;
$mq__cols-vert: 1140px;

//line-height
$line-height_xs: 1;
$line-height_s: 1.2;
$line-height: 1.5;
$line-height_l: 1.7;

// Dimentions
$dim__header_height: 50px;
$space__side: 40px;
$space__vside: 48px;
$space__vside_t: 34px;
$space__vside_m: 20px;
$space__side_m: 20px;
$space__bottom: 24px;
$space__bottom_s: 12px;
$space__input-side: 12px;

$height__input: 56px;
$height__input_m: 44px;

$border-radius: 4px;
$content-max: 800px;
$content-max_thin: 570px;

$mq__masonry_auto: 400px;

$h-list-bottom-space: 50px;
$h-list-bottom-space_t: 40px;
$h-list-bottom-space_m: 30px;

// Animation
$animation__delay: 0.15s;
$animation__duration_slow: 0.6s;
$animation__duration_slowest: 0.8s;
$animation__duration: 0.3s;
$animation__duration_fast: 0.2s;

$ease__opacity: cubic-bezier(0.165, 0.84, 0.44, 1); // easeOutQuart
$ease__transform: cubic-bezier(0.19, 1, 0.22, 1); // $easeOutExpo

// Other
$spinner-dim: 38px;
$spinner-border: 4px;

$spinner-opacity: 0.3;

$box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.14);
$box-shadow_bottom: 0 5px 5px -5px rgba(0, 0, 0, .5);
