.chat-hidden {
    position: fixed;
    display: none;
    left: 30px;
    bottom: 104px;
    z-index: 102;
    max-width: 361px;
    width: 100%;
    @media (max-width: $mq__mobile) {
        left: 10px;
        max-width: calc(100vw - 20px);
    }
    &.is-display {
        display: block;
    }

    &__close {
        position: absolute;
        background: none;
        border: none;
        right: 12px;
        top: 12px;
        cursor: pointer;
        svg {
            width: 12px;
            height: 12px;
        }
    }

    &__send {
        padding-right: 20px;
        padding-left: 16px;
        border: none;
        width: 100%;
        background: #ffffff;
        box-shadow: 0px 0px 20px #dbdbdb;
        border-radius: 10px;
        height: 56px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        cursor: pointer;
        div {
            margin: 0;
            font-size: 14px;
            line-height: 100%;
            span {
                color: #22222280;
            }
        }
        svg {
            width: 19px;
            height: 16px;
        }
    }
    &__text {
        background: #ffffff;
        box-shadow: 0px 0px 20px #dbdbdb;
        border-radius: 10px;
        padding: 10px 16px;
        margin-bottom: 10px;
        p {
            font-size: 14px;
            line-height: 100%;
            color: rgba(34, 34, 34, 0.8);
        }
    }
    
    &__people {
        margin-bottom: 16px;
        display: flex;
        align-items: center;
        gap: 10px;
        p {
            margin: 0;
            font-weight: 600;
            font-size: 14px;
            line-height: 100%;
            color: #222222;
        }
        picture {
            border-radius: 100%;
        }
    }
}
.chat-showed {
    height: fit-content;
    position: fixed;
    display: none;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin: auto;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    box-shadow: 0px 0px 20px #dbdbdb;
    max-width: 856px;
    width: 100%;
    padding: 24px;
    z-index: 103;
    background: #EEF2F5;
    border-radius: 30px;
    @media (max-width: $mq__mobile) {
        padding: 12px;
        max-width: calc(100vw - 32px);
        max-height: 100vh;
        overflow: auto;
        justify-content: start;
    }
    &.is-display {
        display: flex;
    }

    &__row{
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 48px;
        @media (max-width: $mq__mobile) {
          flex-direction: column;
          gap: 12px;  
        }
    }

    &__block{
        max-width: 346px;
        width: 100%;
        picture img{
            margin: 0 auto;
            margin-bottom: 10px;
            @media (max-width: $mq__mobile) {
                max-width: 64px;
                margin-bottom: 6px;
            }
        }
        h3{
            font-weight: 600;
            font-size: 22px;
            line-height: 26px;
            text-align: center;
            letter-spacing: 0.01em;
            margin-bottom: 6px;
            color: #222222;
            @media (max-width: $mq__mobile) {
                font-size: 18px;
                line-height: 21px;
                margin-bottom: 4px;
            }
        }
        .chat-showed__subname{
            font-weight: 400;
            font-size: 14px;
            line-height: 16px;
            text-align: center;
            letter-spacing: 0.01em;
            color: #222222;
            opacity: 0.7;
            margin-bottom: 16px;
            @media (max-width: $mq__mobile) {
                font-size: 12px;
                line-height: 14px;
                margin-bottom: 8px;
            }
        }
        .chat-showed__list{
            display: flex;
            flex-direction: column;
            gap: 10px;
            margin-bottom: 16px;
            @media (max-width: $mq__mobile) {
                gap: 8px;
                margin-bottom: 0;
            }
            p{
                margin: 0;
                display: flex;
                align-items: center;
                gap: 10px;
                font-weight: 600;
                font-size: 16px;
                line-height: 19px;
                letter-spacing: 0.01em;
                color: #222222;
                @media (max-width: $mq__mobile) {
                    font-size: 14px;
                    line-height: 16px;
                    gap: 4px;
                }
                svg{
                    width: 22px;
                    height: 16px;
                    @media (max-width: $mq__mobile) {
                        width: 14px;
                        height: 11px;
                    }
                }
            }
        }
        .chat-showed__text{
            font-weight: 500;
            font-size: 16px;
            line-height: 150%;
            color: #222222;
            opacity: 0.7;
            @media (max-width: $mq__mobile) {
                display: none;
            }
        }
    }

    &__content{
        background: #FFFFFF;
        border-radius: 20px;
        max-width: 414px;
        width: 100%;
        padding: 24px;
        @media (max-width: $mq__mobile) {
            padding: 10px;
        }
        h2{
            font-weight: 700;
            font-size: 36px;
            line-height: 105%;
            color: #222222;
            margin-bottom: 24px;
            @media (max-width: $mq__mobile) {
                font-size: 18px;
                line-height: 120%;
                margin-bottom: 12px;
            }
            b{
                color: #3055D8;
            } 
        }
    }


    &__call{
        display: flex;
        flex-direction: column;
        gap: 12px;
        margin-bottom: 24px;
        @media (max-width: $mq__mobile) {
            margin-bottom: 12px;
            gap: 8px;
        }
        p{
            margin: 0;
            display: flex;
            margin: 0;
            font-weight: 500;
            font-size: 14px;
            line-height: 150%;
            color: rgba(34, 34, 34, 0.7);
            @media (max-width: $mq__mobile) {
                font-size: 12px;
                line-height: 140%;
            }
            a{
                font-weight: 600;
                color: #3055D8;
                display: flex;
                align-items: center;
                &:first-child{
                    margin-left: 10px;
                    @media (max-width: $mq__mobile) {
                        margin-left: 5px;
                        margin-right: 5px;
                    }
                }
                @media (max-width: $mq__mobile) {
                    font-size: 0;
                }
                svg{
                    margin-left: 7.5px;
                    display: inline;
                    width: 18px;
                    height: 18px;
                    margin-right: 10px;
                    @media (max-width: $mq__mobile) {
                        margin: 0;
                    }
                }
            }
        }
    }

    &__tel{
        @media (max-width: $mq__mobile) {
            flex-direction: column;
        }
        a{
            @media (max-width: $mq__mobile) {
                font-size: 14px !important;
            }
        }
        
    }

    &__close {
        position: absolute;
        background: none;
        border: none;
        right: -10px;
        top: -45px;
        cursor: pointer;
        @media (max-width: $mq__mobile) {
            right: 10px;
            top: 10px;
        }
        svg {
            width: 32px;
            height: 32px;
            @media (max-width: $mq__mobile) {
                g > path{
                    fill: #3055D8;
                }
            }
        }
    }
    .chat-showed__text-mobile{
        display: none;
        margin-bottom: 12px;
        font-weight: 500;
        font-size: 12px;
        line-height: 124%;
        color: #222222;
        opacity: 0.7;
        @media (max-width: $mq__mobile) {
            display: block;
        }
    }
    form {
        .chat-showed__copy{
            font-weight: 500;
            font-size: 12px;
            line-height: 130%;
            color: #222222;
            opacity: 0.5;
            a{
                display: inline;
            }
        }
        button {
            width: 100%;
            background: #F64F4F;
            border-radius: 20px;
            height: 56px;
            border: none;
            font-weight: 500;
            font-size: 20px;
            line-height: 150%;
            color: white;
            cursor: pointer;
            transition: all 1.5s ease;
            margin-bottom: 12px;
            &:hover {
                opacity: 0.8;
                transition-duration: 0.6s;
            }
            @media (max-width: $mq__mobile) {
                margin-bottom: 8px;
                height: 45px;
            }
        }
        input {
            background: #ffffff;
            border: 1px solid #3055D8;
            border-radius: 16px;
            height: 52px;
            width: 100%;
            padding-left: 16px;
            font-family: "TT Hoves";
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 150%;
            color: #22222280;
            margin-bottom: 24px;
            @media (max-width: $mq__mobile) {
                margin-bottom: 12px;
                height: 45px;
            }
        }
    }
}
.chat-button {
    position: fixed;
    bottom: 20px;
    left: 20px;
    width: 64px;
    height: 64px;
    background: #ffffff;
    box-shadow: 0 0 20px #dbdbdb;
    border-radius: 100px;
    z-index: 10;
    cursor: pointer;
    border: none;
    margin: 0;
    padding: 17px;
    svg {
        width: 30px;
        height: 30px;
    }
}

#umnico-widget-wrapper {
    position: relative;
    z-index: 3;

    &::after {
        content: "";
        pointer-events: none;
        position: fixed;
        border-radius: 100%;
        left: 27px;
        bottom: 22px;
        background: white;
        background-position: 18px 18px;
        background-repeat: no-repeat;
        background-image: url("data:image/png;base64,");
        width: 67px;
        height: 67px;
        z-index: 2147483646;
    }
}
